@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), local("Roboto-Regular"), url("https://fonts.gstatic.com/s/roboto/v15/2UX7WLTfW3W8TclTUvlFyQ.woff") format("woff"); }

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  src: local("Roboto Bold"), local("Roboto-Bold"), url("https://fonts.gstatic.com/s/roboto/v15/d-6IYplOFocCacKzxwXSOD8E0i7KZn-EPnyo3HZu7kw.woff") format("woff"); }

html {
  background: #fafafa; }

html,
body {
  margin: 0;
  padding: 0;
  font-family: roboto, sans-serif;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

button:-moz-focus-inner {
  border: 0;
  padding: 0; }

button {
  cursor: pointer; }

.layout {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
  flex-flow: column;
  height: 100%; }

.main {
  -webkit-flex: 1;
  flex: 1;
  display: -webkit-flex;
  display: flex;
  overflow: hidden;
  position: relative; }

html,
body {
  height: 100%;
  overflow: hidden; }

.scroller {
  overflow: hidden;
  overflow-y: scroll;
  width: 100%; }
